@use "../../App.scss" as *;
.normal_contanier {
  display: flex;
  background-color: #000;
  margin-top: 3rem;
  @include breack_down(large) {
    margin-top: 2.2rem;
  }
  .blog_hero_width {
    width: 79%;
    @include breack_down(large) {
      width: 100%;
    }
  }

  @include breack_down(large) {
    display: block;
  }
  .normal_cards_container {
    padding-right: 2rem;
    background-color: #000;
    @include breack_down(large) {
      padding: 0;
      overflow: hidden;
    }
    h2 {
      color: #fff;
      padding: 2rem 0;
      font-size: 28px;
      font-weight: 400;
      @include breack_down(large) {
        padding: 2rem 0 0 0;
        text-align: center;
        font-size: 20px;
      }
    }
    .normal_cards {
      display: flex;
      justify-content: center;
      gap: 1%;
      row-gap: 2rem;
      flex-wrap: wrap;
      width: 100%;
      padding: 2rem 0;
    }
  }
}
.display_none_mobile {
  @include breack_down(large) {
    display: none;
  }
}
.display_none_desktop {
  @include breack_up(medium) {
    display: none;
  }
}
.blog_hero_container {
  background-color: #000;
  width: 100%;
  padding-right: 2rem;
  overflow: hidden;
  @include breack_down(large) {
    padding: 0 1rem;
  }
  .blog_hero {
    background-color: #181919;
    padding: 2rem;
    border-radius: 16px;
    @include breack_down(large) {
      padding: 0.5rem;
      border-radius: 10px;
    }
    .bh_image {
      margin-bottom: 1rem;
      @include breack_down(large) {
        // margin: 0;
      }
      img {
        width: 100%;
        object-fit: cover;
        border-radius: 18px;
        border: 1px solid #75e69f;
        @include breack_down(large) {
          border-radius: 10px;
        }
      }
    }
    .blog_hero_btns {
      display: flex;
      justify-content: space-between;
      @include breack_down(large) {
        flex-direction: column;
        gap: 1rem;
        align-items: center;
      }
      .blog_hero_likes_btn {
        display: flex;
        gap: 1rem;
        .bh_likes {
          display: flex;
          border: 1px solid #505050;
          border-radius: 8px;
          button {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: transparent;
            padding: 8px 0;
            width: 10rem;
            gap: 5px;
            border: none;
            color: #d8d8d8;
            font-size: 22px;
            cursor: pointer;
            @include breack_down(large) {
              width: 5rem;
              font-size: 12px;
            }
            img {
              width: 32px;
              height: 32px;
              @include breack_down(large) {
                width: 20px;
                height: 20px;
              }
            }
          }
          .db_left_b {
            border-left: 1px solid #505050;
          }
        }
        .faw_btn {
          background-color: transparent;
          border: 1px solid #ff00b8;
          border-radius: 8px;
          display: flex;
          align-items: center;
          gap: 5px;
          color: #d8d8d8;
          padding: 8px 0;
          justify-content: center;
          font-size: 22px;
          width: 10rem;
          cursor: pointer;
          @include breack_down(large) {
            width: 5rem;
            font-size: 12px;
          }
          img {
            width: 32px;
            height: 32px;
            @include breack_down(large) {
              width: 20px;
              height: 20px;
            }
          }
        }
      }
      .blog_hero_tips {
        display: flex;
        gap: 1rem;
        button {
          cursor: pointer;
        }
        .tips_btn {
          display: flex;
          align-items: center;
          width: 7rem;
          justify-content: center;
          color: #eaaa08;
          background-color: transparent;
          border: 1px solid #eaaa08;
          border-radius: 8px;
          gap: 5px;
          @include breack_down(large) {
            width: 5rem;
            font-size: 12px;
          }
          img {
            @include breack_down(large) {
              width: 20px;
              height: 20px;
            }
          }
        }
        .dow_btn {
          display: flex;
          align-items: center;
          width: 10rem;
          justify-content: center;
          color: #fff;
          background-color: #15b79e;
          border: 1px solid #15b79e;
          border-radius: 8px;
          gap: 5px;
          @include breack_down(large) {
            width: 7rem;
            font-size: 12px;
          }
        }
      }
    }
    .blog_hero_stats {
      display: flex;
      justify-content: space-between;
      margin: 3rem 0;
      @include breack_down(large) {
        flex-direction: column;
        gap: 1rem;
        margin: 2rem 0;
      }
      .blog_hero_info {
        display: flex;
        align-items: center;
        gap: 2rem;
        @include breack_down(large) {
          justify-content: center;
        }
        p {
          color: #d8d8d8;
          font-size: 16px;
          font-weight: 400;
          @include breack_down(large) {
            font-size: 12px;
          }
        }
        h3 {
          font-weight: 400;
          font-size: 28px;
          color: #fff;
          @include breack_down(large) {
            font-size: 20px;
          }
        }
        img {
          width: 88px;
          height: 88px;
          border-radius: 50%;
          @include breack_down(large) {
            width: 50px;
            height: 50px;
          }
        }
      }
      .blog_hero_stats_btns {
        display: flex;
        gap: 2.5rem;
        @include breack_down(large) {
          justify-content: center;
          align-items: center;
        }
        img {
          height: 30px;
          @include breack_down(large) {
            height: 18px;
          }
        }
        button {
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 27px;
          background-color: transparent;
          border: none;
          gap: 12px;
          color: #fff;
          @include breack_down(large) {
            font-size: 14px;
          }
        }
        .bhs_like {
          img {
            filter: invert(76%) sepia(60%) saturate(6965%) hue-rotate(110deg)
              brightness(97%) contrast(103%);
          }
        }
        .bhs_dislike {
          img {
            filter: invert(37%) sepia(64%) saturate(4247%) hue-rotate(350deg)
              brightness(97%) contrast(93%);
          }
        }
        .bhs_fav {
          img {
            filter: invert(18%) sepia(70%) saturate(6679%) hue-rotate(310deg)
              brightness(107%) contrast(114%);
          }
        }
      }
    }
    .blog_hero_text {
      p {
        color: #d8d8d8;
        @include breack_down(large) {
          font-size: 13px;
        }
      }
      .breack {
        height: 1rem;
      }
    }
  }
}

.blog_carousel_container {
  padding: 48px 40px;
  border-radius: 16px;
  background-color: #202020;
  display: flex;
  flex-direction: column;
  gap: 32px;
  margin-top: 32px;
  @include breack_down(large) {
    padding: 19px 10px;
    gap: 16px;
  }
  h3 {
    color: #ffffff;
    font-size: 28px;
    font-weight: 400;
    @include breack_down(large) {
      font-size: 18px;
    }
  }
}
.normal_input_section {
  display: flex;
  align-items: flex-start;
  gap: 25px;
  margin-top: 2rem;
  img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
  }
  textarea {
    width: 100% !important;
    min-height: 7rem;
    max-height: 14rem;
    background-color: #101010;
    padding: 16px;
    border-radius: 14px;
    color: #fff;
    border: none;
    &:focus {
      outline: none;
    }
  }
}
